html,
body {
  margin: 0px;
}

.SignIn {
  background: url(../../static/media/sign_in.66623ce5.jpg) center center / cover no-repeat;
}

.card {
  background-color: whitesmoke;
  opacity: 0.8;
  height: 450px;
  width: 300px;
  margin-top: auto;
  margin-bottom: auto;
}

.card-header {
  color: #3c240f;
}

.card-body {
  color: #3c240f;
}

